.neon-gold {
  color: var(--aurum-gold);
  text-shadow: 0 0 10px rgba(245, 183, 0, 0.5);
}
.neon-blue {
  color: var(--electric-blue);
  text-shadow: 0 0 10px rgba(0, 209, 255, 0.5);
}
.neon-violet {
  color: var(--violet-flux);
  text-shadow: 0 0 10px rgba(123, 97, 255, 0.5);
}
.neon-pink {
  color: var(--neon-pink);
  text-shadow: 0 0 15px rgba(255, 42, 109, 0.6);
}
.scanlines {
  position: relative;
  overflow: hidden;
}
.scanlines::before {
  content: "";
  position: absolute;
  inset: 0;
  background: repeating-linear-gradient(
    0deg,
    rgba(255, 255, 255, 0.03) 0px,
    transparent 2px,
    transparent 4px
  );
  pointer-events: none;
  z-index: 1;
}
.noise {
  position: relative;
  overflow: hidden;
}
.noise::after {
  content: "";
  position: absolute;
  inset: 0;
  background-image: url("data:image/svg+xml,%3Csvg viewBox='0 0 400 400' xmlns='http://www.w3.org/2000/svg'%3E%3Cfilter id='noise'%3E%3CfeTurbulence type='fractalNoise' baseFrequency='0.9' numOctaves='4' stitchTiles='stitch'/%3E%3C/filter%3E%3Crect width='100%25' height='100%25' filter='url(%23noise)' opacity='0.05'/%3E%3C/svg%3E");
